import { defineStore } from 'pinia'

export const useOrdersStore = defineStore('orders', {
  state: () => {
    return {
      viewOrders: false,
      orders: [],
    }
  },

  actions: {
    showOrders(orders: []) {
      this.viewOrders = true
      this.orders = orders
    },

    hideOrders() {
      this.viewOrders = false
      this.orders = []
    },
  },
})
